
.todo-list{
    text-align: center;
   height: 50%;
}
  .diva{
    text-decoration: none;
  }
  .toggle{
    text-decoration: line-through;
  
  }
  
h2{
    text-align: center;
}

  .input{
border-radius: 10px;
  }

  .tasks{
    margin:10px 50px;
    height: 100%;
 border: 2px solid white;
    border-radius: 10px;
    font-size: 1rem;
   
  }

  .items{
 display: flex;
    justify-content: space-between;
   margin:0px 20px;
  }

  .delete{
    border-radius: 50%;
    background-color: aqua;
  }


  @media(max-width:530px){
    .items{
      margin:0px 5px;
    }
    .tasks{
      margin: 10px 10px;
      font-size: 15px;
    }
  }

  @media(max-width:420px){
    .tasks{
      margin: 10px 20px;
      font-size: 10px;
    }
  }

  @media(max-width:317px){
    .tasks{
      margin: 10px 10px;
      font-size: 9px;
    }
  }

  @media(max-width:270px){
    .tasks{
      font-size: 7px;
    }
  }

