
.Todo{

    background-color: #50C2C9;
    width: 500px;
    height: 100vh; 
    border-radius: 20px;
}

.todolist{
    height: 100%;

}

.avatar, h3, .tasklist{
    text-align: center;
    color: aliceblue;
}

.avatar{
    height: 100px;
   margin-left: 40px;
}

@media(max-width:530px){
.Todo{
    width: 400px;
}
.avatar{
    height: 80px;
   margin-left: 0px;
}
}


@media(max-width:420px){
    .Todo{
        width: 300px;
    }
    .avatar{
        height: 60px;
       margin-left: -40px;
    }
    }

    @media(max-width:317px){
        .Todo{
            width: 250px;
        }
        .avatar{
            height: 50px;
           margin-left: -50px;
        }
        }

        
    @media(max-width:270px){
        .Todo{
            width: 200px;
        }
        .avatar{
           margin-left: -70px;
        }
        }

     